      </div>

      <div class="nose" @click="handleTopic">
        <div class="topic">{{topicObj[topic]}}</div>

        <div class="click-me">click</div>
      </div>

      <div :class="['mouth',bite && 'bite']">
        <div class="item">
          <template v-if="!bite && topic === 'works'">
            <div class="works">
              <div class="works-item">
