                <div class="content">
                  SenseFace 3.0+
                  <br />SenseCity 4.0 重构
                  <br />SenseSkyline 智慧城市
                  <br />SenseGuard 疫情系统
                </div>
              </div>
            </div>
          </template>
          <template v-if="!bite && topic === 'skill'">
            <div class="skill">
              <div
                class="skill-item"
                v-for="item in ['Vue','Html','Css','Nodejs','React','微信小程序','JQuery']"
                :key="item"
              >{{item}}</div>
            </div>
          </template>

          <template v-if="!bite && topic === 'information'">
            <div class="information">
              <div class="in-item">
                <div class="label">address</div>
                <div class="image">深圳</div>
              </div>
              <div class="in-item">
                <div class="label">Age</div>
                <div class="image">95</div>
              </div>
              <div class="in-item">
                <div class="label">github</div>
                <div class="image">
                  <svg
                    height="32"
                    class="octicon octicon-mark-github text-white"
                    viewBox="0 0 16 16"
                    version="1.1"
                    width="32"
                    aria-hidden="true"
                  >
                    <path
                      fill-rule="evenodd"
                      d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0016 8c0-4.42-3.58-8-8-8z"
                    />
                  </svg>
                </div>
              </div>
              <div class="in-item">
                <div class="label">mail</div>
                <div class="image">
                  <img
                    src="//mimg.127.net/hxm/dashi-home/p/20151107/style/img/newHome/section2/02_163mail-f07ddd860b.png"
                    alt
                  />
                </div>
              </div>
              <div class="in-item">
                <div class="label">WeChat</div>
                <div class="image"></div>
              </div>
              <div class="in-item">
                <div class="label">QQ</div>
                <div class="image"></div>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import RainbowWaves from "rainbow-waves";

export default {
  name: "Zi",
  components: {
    RainbowWaves,
  },
  computed: {},
  watch: {},
  data() {
    return {
      config: {
        background: {
          type: "color",
          color: "DarkCyan",
        },
        direction: "top",
      },
      waves: [
        {
          background: {
            type: "color",
            color: "rgba(245, 194, 66,.9)",
            position: [0, 0, 0, 1080],
          },
          jitter: 0.04,
          restore: 0.03,
          waveGap: 80,
          waterGap: 20,
          waveUps: 6,
          bit: 0.32,
        },
      ],
      topic: "skill",
      bite: false,
      biteTimer: null,
      topicObj: {
        works: "工作经验",
        skill: "职业技能",
        information: "个人信息",
      },
    };
  },
  watch: {
    bite: {
      handler(bite) {
        if (bite) {
          if (this.biteTimer) {
            clearTimeout(this.biteTimer);
          }
          this.biteTimer = setTimeout(() => {
            this.bite = false;
          }, 500);
        }
      },
    },
  },
  mounted() {},
  beforeDestroy() {},
  methods: {
    open() {
      window.open("https://github.com/z7xisoo", "_blank");
    },
    handleTopic() {
      const topic = this.topic;
      switch (topic) {
        case "skill":
          this.topic = "works";
          break;
        case "works":
          this.topic = "information";
          break;
        case "information":
          this.topic = "skill";
          break;
        default:
          this.topic = "skill";
          break;
      }
      this.bite = true;
    },
  },
};
</script>

<style lang="scss" scoped>
#rainbow-waves {
  position: absolute;
