  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
  position: relative;
}
@keyframes view_action {
  form {
    left: 0%;
    transform: rotate(0);
  }
  to {
    left: 100%;
    transform: rotate(4800deg);
  }
}
@keyframes jump_action {
  10% {
    bottom: 0;
  }
  20% {
    bottom: 80%;
  }
  60% {
    bottom: 0;
  }
  64% {
    bottom: 20%;
  }
  70% {
    bottom: 0;
  }
  72% {
