  height: 40%;
  position: fixed;
  top: 0;
  left: 0;
  font-family: fantasy, cursive, monospace,fangsong,serif;
  font-size: 1.2rem;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  padding: 0 1rem;
  flex-direction: row;
  box-sizing: border-box;
