    height: 2rem;
    display: block;
    position: absolute;
    left: -50%;
    bottom: 0;
    border-radius: 50%;
    animation: 5s ease-in 2s infinite view_action;
    &:nth-child(1) {
      animation: 6s ease-in 0s infinite view_action,
        6s ease-in 0s infinite jump_action;
      background: linear-gradient(135deg, red, orange, green, blue, DarkCyan);
    }
    &:nth-child(2) {
      animation: 6s ease-in 2s infinite view_action,
        6s ease-in 2s infinite jump_action;
      background: radial-gradient(#00cd66, #00cd66 50%, #fff);
    }
    &:nth-child(3) {
      animation: 6s ease-in 4s infinite view_action,
        6s ease-in 4s infinite jump_action;
      background: radial-gradient(red, red 80%, #000 90%);
    }
  }
}
.mouth {
  width: 100%;
  height: 40%;
  box-sizing: border-box;
  position: absolute;
  bottom: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
